<template>
<div id="box">
<ul id="con1" ref="con1" :class="{anim:animate==true}">
<li v-for="item in items" :key="item.id" @mouseenter="stopscoll" @mouseleave="startscoll" >
    <div style="width:200px;height:150px;overflow:hidden;text-align:left;">
             {{item.name}}
    </div>
  
    
    </li>
</ul>
</div>
</template>
<script>
export default {
data() {
return {
timer:null,
animate:false,
items:[
{name:""},
          {name:""},
          {name:""},
          {name:""},
          {name:"00"},
          {name:""},
          {name:""},
]
}
},
created(){
clearInterval(this.timer)
this.timer = null
this.setTimer()
},
destroyed: function () {
// 
clearInterval(this.timer)
this.timer = null
},
methods: {
stopscoll(){
clearInterval(this.timer)
this.timer=null
},
startscoll(){
clearInterval(this.timer)
this.timer = null
this.setTimer()
},
setTimer(){
var that = this;
if(that.timer==null){
that.timer=setInterval(this.scroll,2000)
}
}, 
scroll(){
this.animate=true; // css3true
setTimeout(()=>{ // es6this
this.items.push(this.items[0]); // 
this.items.shift(); //
this.animate=false; // margin-top 0 
},500)
}
}
}
</script>
<style scoped>
*{
margin: 0 ;
padding: 0;
}
#box{
width: 300px;
height:300px;
overflow: hidden;
padding-left: 30px;
}
.anim{
transition: all 0.5s;
margin-top: -150px;
}
#con1 li{
list-style: none;
/* line-height: 30px; */
height: 150px;
cursor: pointer;
color: #fff;

}
</style>



